/* App.css */

.container {
  display: grid;
  grid-template-columns: 4fr 200px;
  grid-template-rows: 40px 1fr;
  grid-gap: 10px;
  width: 80vw;
  margin: 0 auto;
  padding: 20px;
  height: 100vh;
}

.title {
  grid-column: 1 / 3;
  text-align: center;
  padding: 10px;
  margin-bottom: 20px;
  font-size: 24px;
  user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
}

.content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 70px 1fr 1fr;
  grid-gap: 10px;
}

.summary {
  grid-column: 1 / 3;
  padding: 10px;
  display: flex;
  justify-content: space-between;
}

.summary .infos {
  display: flex;
  border: solid 1px #ccc;
  border-radius: 3px;
  width: 20%;
  text-align: center;
  font-size: 1rem;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 10px;
  background: #fff;
}

.chart {
  padding: 10px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}

.GridBox3 {
  display: grid;
  justify-content: space-between;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  border-radius: 6px;
  /* box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1); */
  height: 100%;
  width: 100%;
}

.GridBox3 .GridBox3-Item {
  /* box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1); */

}

.GridBox3 .GridBox3-Item:nth-child(2) {}

.GridBox3 .GridBox3-Item:nth-child(3) {
  grid-column: span 2;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: stretch;
}

.chart3s {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 30%;
}

.chart2s {
  display: grid;
  justify-content: space-between;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr;
  border-radius: 6px;
  /* box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1); */
  height: 100%;
  width: 100%;
}

.chart2s_item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.chart .userwrap {
  max-height: 40vh;
  overflow-y: scroll;
  height: auto;
  margin: auto;
}



.tabs {
  display: flex;
  margin-bottom: 20px;
  width: 100%;
}

.tab-button {
  flex: 1 1;
  padding: 10px 0px;
  font-size: 13px;
  font-weight: 600;
  border: none;
  background-color: #fff;
  cursor: pointer;
  white-space: nowrap;
}

.tab-button.active {
  text-decoration: underline #333 2px;
  text-underline-offset: 5px;
}





.userwrap .mentions {
  overflow-y: auto;
}

.userwrap .mentions::-webkit-scrollbar {
  width: 8px;
  background-color: transparent;
}

.userwrap .mentions::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}

.userwrap .mentions li {

  padding: 5px 0px 0px 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  line-height: 1.2;
  margin-bottom: 10px;
}

.userwrap .mentions .postwrap {
  padding: 5px 0px 0px 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  line-height: 1.2;
  margin-bottom: 10px;
  display: flex;
}

.userwrap .mentions .postwrap2 {
  padding: 5px 0px 0px 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  line-height: 1.2;
  margin-bottom: 10px;

}

.vertical-text {
  writing-mode: vertical-rl;
  text-orientation: mixed;
  white-space: nowrap;
  /* 필요에 따라 설정 */
  transform: rotate(-180deg);
  text-align: center;
}

.userwrap .mentions .postwrap .contents {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
}

.flx_spbt {
  display: flex;
  justify-content: space-between;
    width: 100%;
}

.flx_spbt div span {
  padding-left: 5px;
}

.sidebar {
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}

.sidebar .userwrap .mentions {
  padding: 10px;
  max-height: 91vh
}


/* modal */
/* 모달을 화면 전체에 가득 차게 만듭니다. */
.sidebarmenu {

  position: fixed;
  top: 0;
  right: 0;
  color: #333;
  background-color: #fff;
  /* 헤더 배경색을 원하는 색상으로 변경하세요 */
  z-index: 999;
  /* 다른 요소 위에 헤더 메뉴가 나타나도록 설정합니다. */
  transition: top 0.3s;
  /* 스크롤 시 부드럽게 이동하도록 설정합니다. */
  text-align: left;
  padding: 20px 20px 10px 10px;
  width: auto;
  border: none;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background-color: rgba(0, 0, 0, 0.5);  */
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
  /* 모달이 다른 요소 위에 표시되도록 설정합니다. */
}

/* 모달 내용을 스타일링합니다. */
.modal .modal-sidebar {
  background-color: #fff;
  /* 모달 내부 배경색 */
  border-radius: 10px;
  max-width: 90%;
  /* 모달 내용의 최대 너비를 설정합니다. */
  max-height: 70%;
  /* 모달 내용의 최대 높이를 설정합니다. */
  overflow-y: auto;
  /* 모달 내용이 내용보다 크면 스크롤 가능하도록 설정합니다. */
  box-shadow: 4px 5px 9px rgba(0, 0, 0, 0.2);
}

.modal .tabs {
  position: sticky;
  top: 0;
  /* 원하는 위치에 고정하려면 조절 */
  background-color: #fff;
  /* tabs의 배경색 */
  z-index: 1;
  /* 다른 요소 위에 표시되도록 설정 */
}

/* 탭 버튼 스타일 */
.modal .tab-button {
  background-color: transparent;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 1em;
  background: rgba(0, 0, 0, 0.1);
  border-radius: 3px;
}

/* 활성화된 탭 버튼 스타일 */
.modal .tab-button.active {
  background-color: #007bff;
  /* 활성화된 탭 버튼 배경색 */
  color: #fff;
  /* 활성화된 탭 버튼 텍스트 색상 */
}

.modal .close-modal {
  position: absolute;
  top: 15px;
  left: 15px;
  background: #fff;
  border: none;
}

/* 모달 내용의 리스트 스타일링 */
.modal ul.mentions {
  list-style: none;
  padding: 0;
}

.modal ul.mentions li {
  margin-bottom: 10px;

  padding: 0px 5px 5px 5px;
}

