/* 모바일 세로 (해상도 ~ 479px)*/
@media all and (max-width:479px) {
  h2 {
    font-size: 0.8em;
  }

  .summary {
    padding: 0;
  }

  .infos p {
    font-size: 0.6em;
  }

  .tabs button {
    font-size: 0.6em;
  }

  .container {
    display: flex;
    flex-direction: column;
    grid-template-columns: none;
  }

  .chart .chart3s {
    flex-direction: row;
  }

  .content {
    display: flex;
    flex-direction: column;
    grid-template-columns: 1fr;
    /* 차트 1개씩 나열 */
